* {
  padding: 0;
}
.allheader {
  align-items: center;
  padding: 5px 20px;
  background-color: #191e2b;
  color: #ffffff;
}
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* padding: 20px; */
}

.header__left {
  margin-left: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__leftStepper {
  margin-left: 8px;
}
.header__right {
  margin-right: 10px;
}
.header__rightbtn {
  margin-right: 8px;
}
.header__logo {
  height: 35px;
  width: 35px;
}

/* body css */
.body {
  display: flex;
  justify-content: space-between;
}
/* leftbar css */
.LeftSideBar {
  position: relative;
  flex: 0.18 1;
  height: 78vh;
  color: #ffffff;
  /* overflow-x: auto; */
}

/* workspace css */
.WorkSpace {
  min-width: 50vw;
  flex: 0.7 1;
  margin-top: 1.5vh;
  height: 77.8vh;
  justify-content: center;
  /* overflow-x: auto; */
}

/* RightBar1 css */
.RightSideBar1 {
  flex: 0.22 1;
  background-color: #252935;
  padding: 10px 10px;
  height: 82.7vh;
  /* overflow-x: auto; */
}

.RightSideBar1Btns {
  display: flex;
}

.RightSideBar1Btn {
  background-color: #393e49;
  margin-right: 5px;
  color: #ffffff;
}

.RightSideBar1__Padding {
  display: flex;
}

.RightSideBar1__Color {
  display: flex;
}

.RightSideBar1__Text {
  margin-top: 40px;
  margin-bottom: 5px;
  color: #5a5d68;
  font-weight: 700;
  font-size: 12px;
}

/* RightSideBar2 Css */
.RightSideBar2 {
  flex: 0.22 1;
  background-color: #252935;
  padding: 10px 10px;
  height: 82.7vh;
  /* overflow-y: auto;
  overflow-x: auto; */
}

.RightSideBar2__Btn {
  display: flex;
  align-items: center;
  padding: 5px 10px;
  margin-top: 5px;
  border-radius: 5px;
  margin-right: 5px;
  background-color: #70cff3;
}

.RightSideBar2__Btn__icon {
  color: #ffffff;
  font-size: large !important;
}

.RightSideBar2__Btn__title {
  flex: 1 1;
  margin-left: 5px;
  font-weight: 700;
  font-size: 16px;
}

.RightSideBar2__Btn:hover {
  background-color: #068dc0;
  cursor: pointer;
}

.RightSideBar2__Text {
  margin-top: 20px;
  margin-bottom: 5px;
  color: #5a5d68;
  font-weight: 700;
  font-size: 15px;
}

.scrollhost::-webkit-scrollbar {
  display: none;
}

.scrollhost-container {
  position: relative;
  height: 100%;
}
.scrollhost {
  overflow: auto;
  height: 100%;
  scrollbar-width: none;
  -ms-overflow-style: none;
  position: relative;
}

.scroll-bar {
  width: 8px;
  height: 100%;
  right: 0;
  top: 0px;
  position: absolute;
  border-radius: 7px;
  bottom: 0px;
  background-color: #0092e0;
}

.scroll-thumb {
  width: 8px;
  /* height: 20px; */
  /* margin-left: 1px; */
  position: absolute;
  border-radius: 7px;
  opacity: 1;
  top: 0;
  background-color: #ffffff;
  z-index: 1000;
}

.HeaderBtn {
  display: flex;
  align-items: center;
  padding: 10px 20px;
  margin-top: 5px;
  border-radius: 5px;

  background-color: #f36969;
  width: 200px;
}

.HeaderBtn__icon {
  color: #ffffff;
  font-size: large !important;
}

.HeaderBtn__title {
  flex: 1 1;
  margin-left: 5px;
  font-size: 12px;
  color: white !important;
  text-decoration: none !important;
}

.HeaderBtn:hover {
  background-color: #068dc0;

  cursor: pointer;
}

.HeaderBtn.selected {
  background-color: #f36969;
}

